import React, { useState } from 'react'
import Axios from 'axios';
import Swal from 'sweetalert2';
import DateFnsUtils from '@date-io/date-fns';

import {
    Button,
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
    Card,
    CardBody,
    Row,
    Col
} from 'reactstrap';
import TextField from '@material-ui/core/TextField';
import { CircularProgress } from '@material-ui/core';
import InputAdornment from '@material-ui/core/InputAdornment';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faWindowClose, faUser, faSave, faAnchor, faMapMarkedAlt, faShip, faPhoneAlt, faUsers, faTicketAlt, faPeopleArrows } from '@fortawesome/free-solid-svg-icons';

import { useAuth } from "../../../../../context/auth";
import { useUrl } from "../../../../../context/url";

import TypeSelect from '../ProfileFormComponents/TypeSelect';
import GenderSelect from '../ProfileFormComponents/GenderSelect';
import DepartureDate from '../ProfileFormComponents/DepartureDate';
import ArrivalDate from '../ProfileFormComponents/ArrivalDate';
import TransportType from '../ProfileFormComponents/TransportType';
import AccomodationType from '../ProfileFormComponents/AccommodationType';

const ProfileForm = (props) => {
    const authToken = useAuth().authTokens.access_token;
    const urlPath = useUrl().getUrl('carrier/add-profile');
    const dateFns = new DateFnsUtils();
    const [isProcessing, setIsProcessing] = useState(false);

    // Personal
    const [firstName, setFirstName] = useState('');
    const [lastName, setLastName] = useState('');
    const [age, setAge] = useState('');
    const [mobileNumber, setMobileNumber] = useState('');
    const [email, setEmail] = useState('');
    const [bodyTemperature, setBodyTemperature] = useState('');

    //Address
    const [zipcode, setZipcode] = useState('');
    const [completeAddress, setCompleteAddress] = useState('');

    // Travel Details
    const [origin, setOrigin] = useState('');
    const [destination, setDestination] = useState('');
    const [travelHistory, setTravelHistory] = useState('');

    // Transport Details
    const [shipName, setShipName] = useState('');
    const [departureDate, setDepartureDateChange] = useState(null);
    const [arrivalDate, setArrivalDateChange] = useState(null);
    const [seatNumber, setSeatNumber] = useState('');
    const [parkingNumber, setParkingNumber] = useState('');
    const [transportNumber, setTransportNumber] = useState('');
    const [transportRoute, setTransportRoute] = useState('');
    const [transportType, setTransportType] = useState('');

    // Companion Details
    const [childCompanion, setChildCompanion] = useState('');
    const [relativeName, setRelativeName] = useState('');
    const [relativeContact, setRelativeContact] = useState('');

    const saveDataHandler = (e) => {

        e.preventDefault();
        setIsProcessing(true);
        let token = authToken;

        let profileData = {
            // Personal Details
            "person_type": document.querySelector("input[name=personType]").value,
            "first_name": firstName,
            "last_name": lastName,
            "age": age,
            "gender": document.querySelector("input[name=gender]").value,
            "temperature": bodyTemperature,

            // Contact Details
            "mobile": mobileNumber,
            "email": email,

            // Address
            "zipcode": zipcode,
            "specific_address": completeAddress,

            // Travel Details
            "origin": origin,
            "destination": destination,
            "previous_travel": travelHistory,

            // Travel Dates
            "departure": dateFns.format(new Date(departureDate), "Y-M-dd H:m:s"),
            "arrival": dateFns.format(new Date(arrivalDate), "Y-M-dd H:m:s"),
            "accomodation_type": document.querySelector("input[name=accomodationType]").value,

            // Transport Details
            "transport_no": transportNumber, // voyage number
            "route": transportRoute,
            "transport_type": document.querySelector("input[name=transportType]").value,
            "seat_no": seatNumber,
            "parking_space_no": parkingNumber,
            "ship_name": shipName,

            // Child Companion
            "companion": childCompanion,
            //Relative
            "relative_name": relativeName,
            "relative_contact": relativeContact
        }

        Axios.post(urlPath, profileData, {
            headers: {
                'Authorization': 'bearer ' + token,
            }
        }).then(result => {
            setIsProcessing(false);
            if (result.data.status) {
                document.querySelector("input").value = "";
                setDepartureDateChange(null);
                setArrivalDateChange(null);

                Swal.fire(
                    'Transaction Completed!',
                    'Data Successfully Added!',
                    'success'
                );

                props.toggle();
                props.loadProfiles();
            } else {
                Swal.fire(
                    'Transaction Failed!',
                    result.data.message,
                    'error'
                );
            }

        }).catch(e => {
            setIsProcessing(false);
            Swal.fire(
                'Transaction Failed!',
                'Failed to Add New Profile!',
                'error'
            );
        });
    }

    return (
        <div>
            <Modal backdrop={"static"} isOpen={props.isOpen} toggle={props.toggle} className="modal-xl">
                <ModalHeader>
                    New Profile Record <br/>
                    <small className="text-danger p-0 m-0" style={{right:"0"}}>Note: Fields with * requires a value</small>
                </ModalHeader>
                <form onSubmit={(e) => saveDataHandler(e)}>
                    <ModalBody className="pt-0">
                        {
                            isProcessing ?
                                <div className="text-center text-primary" style={{ width: "100%", height: "100%", zIndex: "1001", position: "fixed", top:"0", left:"0"}}>
                                    <div  style={{ backgroundColor:"#fff", opacity: "0.2", width: "100%", height: "100%", zIndex: "1000", position: "fixed", top:"0", left:"0"}}></div>
                                    <CircularProgress style={{ marginTop: "20%", zIndex: "1500",}}/>
                                    <h4>Submitting a new record...</h4>
                                </div>
                                :
                                null
                        }
                        <Row className="p-2">
                            <Col xs={12}>
                                <Row>
                                    <Col xs={12} className="text-left mt-0">
                                        <Card className="p-0" style={{ border: "none" }}>
                                            <CardBody className="m-0  p-0 pl-2 pr-2">
                                                <div className="pt-2 pb-2">
                                                    <TextField
                                                        className="mr-2"
                                                        onChange={(e) => setShipName(e.target.value)}
                                                        InputProps={{
                                                            startAdornment: (<InputAdornment position="start"><FontAwesomeIcon icon={faShip} /></InputAdornment>)
                                                        }}
                                                        name="shipName"
                                                        label="Ship Name"
                                                        required
                                                    />
                                                    <TextField
                                                        className="mr-2"
                                                        onChange={(e) => setTransportNumber(e.target.value)}
                                                        InputProps={{
                                                            startAdornment: (<InputAdornment position="start"><FontAwesomeIcon icon={faAnchor} /></InputAdornment>)
                                                        }}
                                                        name="transportNumber"
                                                        label="Voyage Number"
                                                        required
                                                    />
                                                    <TextField
                                                        className="mr-2"
                                                        onChange={(e) => setSeatNumber(e.target.value)}
                                                        InputProps={{
                                                            startAdornment: (<InputAdornment position="start"><FontAwesomeIcon icon={faTicketAlt} /></InputAdornment>)
                                                        }}
                                                        name="seat_number"
                                                        label="Seat/Bunk Number"
                                                        required
                                                    />
                                                </div>
                                            </CardBody>
                                        </Card>
                                    </Col>
                                </Row>
                            </Col>
                            <Col xs={12} className="mt-3">
                                <Row>
                                    <Col xs={4}>
                                        <h5 className="text-primary"><FontAwesomeIcon icon={faUser} /> Personal Details</h5>
                                        <Row>
                                            <Col xs={12}>
                                                <Card className="p-1">
                                                    <CardBody className="pt-1">
                                                        <div>
                                                            <TypeSelect />
                                                        </div>
                                                        <div>
                                                            <TextField
                                                                name="first_name"
                                                                label="First Name"
                                                                onChange={(e) => setFirstName(e.target.value)}
                                                                fullWidth
                                                                required
                                                            />
                                                        </div>
                                                        <div>
                                                            <TextField
                                                                name="last_name"
                                                                label="Last Name"
                                                                onChange={(e) => setLastName(e.target.value)}
                                                                fullWidth
                                                                required
                                                            />
                                                        </div>
                                                        <div>
                                                            <TextField
                                                                type="number"
                                                                name="age"
                                                                label="Age"
                                                                onChange={(e) => setAge(e.target.value)}
                                                                fullWidth
                                                                required
                                                            />
                                                        </div>
                                                        <div>
                                                            <GenderSelect />
                                                        </div>
                                                        <div className="mb-4">
                                                            <TextField
                                                                type="number"
                                                                name="bodyTemperature"
                                                                label="Body Temperature"
                                                                onChange={(e) => setBodyTemperature(e.target.value)}
                                                                fullWidth
                                                                required
                                                            />
                                                        </div>
                                                    </CardBody>
                                                </Card>
                                            </Col>
                                        </Row>
                                    </Col>
                                    <Col xs={4} className="p-0">
                                        <h5 className="text-primary"><FontAwesomeIcon icon={faPhoneAlt} /> Contact Details</h5>
                                        <Row>
                                            <Col xs={12}>
                                                <Card className="p-1">
                                                    <CardBody className="pt-1">
                                                        <div>
                                                            <TextField
                                                                name="mobile"
                                                                type="number"
                                                                label="Mobile Number"
                                                                onChange={(e) => setMobileNumber(e.target.value)}
                                                                fullWidth
                                                                required
                                                            />
                                                        </div>
                                                        <div>
                                                            <TextField
                                                                type="email"
                                                                label="Email Address"
                                                                onChange={(e) => setEmail(e.target.value)}
                                                                fullWidth
                                                            />
                                                        </div>
                                                    </CardBody>
                                                </Card>
                                            </Col>
                                        </Row>
                                        <h5 className="text-primary mt-1"><FontAwesomeIcon icon={faMapMarkedAlt} /> Address</h5>
                                        <Row>
                                            <Col xs={12}>
                                                <Card className="p-1">
                                                    <CardBody className="pt-1">
                                                        <div className="mt-4">
                                                            <TextField
                                                                name="zipcode"
                                                                label="Zipcode"
                                                                onChange={(e) => setZipcode(e.target.value)}
                                                                fullWidth
                                                                required
                                                            />
                                                        </div>
                                                        <div className="mb-4">
                                                            <TextField
                                                                label="Complete Address"
                                                                name="complete_address"
                                                                onChange={(e) => setCompleteAddress(e.target.value)}
                                                                fullWidth
                                                                required
                                                            />
                                                        </div>
                                                    </CardBody>
                                                </Card>
                                            </Col>
                                        </Row>
                                    </Col>
                                    <Col xs={4}>
                                        <h5 className="text-primary"><FontAwesomeIcon icon={faUsers} /> Companion Details</h5>
                                        <Row>
                                            <Col xs={12}>
                                                <Card className="p-1">
                                                    <CardBody className="pt-1">
                                                        <div className="mt-2 mb-2">
                                                            <TextField
                                                                label="Child Companion"
                                                                name="childCompanion"
                                                                helperText="Use comma (,) as separator for multiple child"
                                                                onChange={(e) => setChildCompanion(e.target.value)}
                                                                fullWidth
                                                            />
                                                        </div>
                                                    </CardBody>
                                                </Card>
                                            </Col>
                                        </Row>
                                        <h5 className="text-primary mt-1"><FontAwesomeIcon icon={faPeopleArrows} /> Nearest Relative</h5>
                                        <Row>
                                            <Col xs={12}>
                                                <Card className="p-1">
                                                    <CardBody className="pt-1">
                                                        <div className="mt-4">
                                                            <TextField
                                                                label="Name"
                                                                name="relativeName"
                                                                onChange={(e) => setRelativeName(e.target.value)}
                                                                fullWidth
                                                                required
                                                            />
                                                        </div>
                                                        <div className="mb-4">
                                                            <TextField
                                                                type="number"
                                                                label="Contact Number"
                                                                name="relativeContact"
                                                                onChange={(e) => setRelativeContact(e.target.value)}
                                                                fullWidth
                                                                required
                                                            />
                                                        </div>
                                                    </CardBody>
                                                </Card>
                                            </Col>
                                        </Row>
                                    </Col>
                                </Row>
                            </Col>
                            <Col xs={12} className="pt-2">
                                <Row>
                                    <Col xs={8}>
                                        <h5 className="text-primary"><FontAwesomeIcon icon={faShip} /> Travel Details</h5>
                                        <Row>
                                            <Col xs={12} className="pr-0">
                                                <Card className="p-1">
                                                    <CardBody className="pt-1">
                                                        <Row>
                                                            <Col xs={6}>
                                                                <div>
                                                                    <TextField
                                                                        label="Port of Embarkation"
                                                                        name="origin"
                                                                        onChange={(e) => setOrigin(e.target.value)}
                                                                        fullWidth
                                                                        required
                                                                    />
                                                                </div>
                                                                <div>
                                                                    <TextField
                                                                        label="Port of Dis-embarkation"
                                                                        name="destination"
                                                                        onChange={(e) => setDestination(e.target.value)}
                                                                        fullWidth
                                                                        required
                                                                    />
                                                                </div>
                                                                <div>
                                                                    <TextField
                                                                        label="Place of Travel History"
                                                                        name="travelHistory"
                                                                        helperText="( within 14 Days )"
                                                                        onChange={(e) => setTravelHistory(e.target.value)}
                                                                        fullWidth
                                                                        required
                                                                    />
                                                                </div>
                                                            </Col>
                                                            <Col xs={6}>
                                                                <div>
                                                                    <DepartureDate departureDate={departureDate} setDepartureDateChange={setDepartureDateChange} />
                                                                </div>
                                                                <div>
                                                                    <ArrivalDate arrivalDate={arrivalDate} setArrivalDateChange={setArrivalDateChange} />
                                                                </div>
                                                                <div>
                                                                    <AccomodationType />
                                                                </div>
                                                            </Col>
                                                        </Row>
                                                    </CardBody>
                                                </Card>
                                            </Col>
                                        </Row>
                                    </Col>
                                    <Col xs={4}>
                                        <h5 className="text-primary"><FontAwesomeIcon icon={faAnchor} /> Other Transport Details</h5>
                                        <Row>
                                            <Col xs={12}>
                                                <Card className="p-1">
                                                    <CardBody className="pt-1">
                                                        <div>
                                                            <TextField
                                                                name="transportRoute"
                                                                label="Voyage Route"
                                                                onChange={(e) => setTransportRoute(e.target.value)}
                                                                fullWidth
                                                                required
                                                            />
                                                        </div>
                                                        <div>
                                                            <TransportType transportType={transportType} setTransportType={setTransportType} />
                                                        </div>
                                                        <div>
                                                            <TextField
                                                                type="number"
                                                                name="parking_space_no"
                                                                label="Parking Space Number"
                                                                helperText="( if with service vehicle )"
                                                                onChange={(e) => setParkingNumber(e.target.value)}
                                                                fullWidth
                                                            />
                                                        </div>
                                                    </CardBody>
                                                </Card>
                                            </Col>
                                        </Row>
                                    </Col>
                                </Row>
                            </Col>
                        </Row>
                    </ModalBody>
                    <ModalFooter>
                        <Button type="submit" color="primary" disabled={isProcessing} style={{ minWidth: "120px" }}>
                            <FontAwesomeIcon icon={faSave} /> Save
                        </Button>{' '}
                        <Button color="danger" onClick={props.toggle} disabled={isProcessing} style={{ minWidth: "120px" }}>
                            <FontAwesomeIcon icon={faWindowClose} /> Close
                    </Button>
                    </ModalFooter>
                </form>
            </Modal>
        </div>
    );
}

export default ProfileForm;