import React, { useState, useEffect, useCallback } from 'react'

import DateFnsUtils from '@date-io/date-fns';
import { useAuth } from "../../../context/auth";
import { useUrl } from "../../../context/url";
import Axios from 'axios';


import ImportForm from './ImportForm/ImportForm';
import DataTable from './DataTable/DataTable';
import Footer from '../Footer/Footer';
import Button from '@material-ui/core/Button';
import EditOutlined from '@material-ui/icons/EditOutlined';
import Tooltip from '@material-ui/core/Tooltip';
import UpdateProfileForm from './ImportForm/Modals/UpdateProfileForm';

const Content = (props) => {
    const token = useAuth().authTokens;
    const urlPath = useUrl().getUrl('carrier/trace-profiles');
    const dateFns = new DateFnsUtils();

    const [profileData, setProfileData] = useState();
    const [isLoading, setIsLoading] = useState(false);

    const [updateProfileModal, setVisibleUpdateProfileModal] = useState(false);
    const [selectedProfile, setSelectedProfile] = useState();
    const toggleUpdateProfileModal = () => setVisibleUpdateProfileModal(!updateProfileModal);

    const columns = [
        "Ref #",
        "Name",
        "Age",
        "Gender",
        "Type",
        "Address",
        "Contact",
        "Embarkation",
        "Disembarkation",
        "ETD",
        "ETA",
        {
            name: "Actions",
            options: {
                customBodyRender: (value, tableMeta, updateValue) => {
                    return (
                        <div>
                            <Tooltip title="Edit Profile" placement="top">
                                <Button size="medium" onClick={() => editProfileHandler(value)} color="primary" variant="text">
                                    <EditOutlined style={{ fontSize: '18' }} />
                                </Button>
                            </Tooltip>
                        </div>
                    )
                }
            }
        }
    ];

    const options = {
        fixHeader: true,
        filterType: 'dropdown',
        responsive: 'vertical',
        tableBodyHeight: '350px',
        selectableRows: 'none'
    };

    const editProfileHandler = (profile) => {
        toggleUpdateProfileModal();
        setSelectedProfile(profile);
    }

    const loadProfiles = useCallback(() => {
        setIsLoading(true);
        Axios.get(urlPath, {
            headers: {
                'Authorization': 'bearer ' + token.access_token
            }
        }).then(result => {
            let profiles;
            if (result !== null && result.data.status !== false) {
                profiles = result.data.map((result) => {
                    let profile = result.profile;

                    let datatimestamp = dateFns.format(new Date(profile.created_at), "MMMM d,yyyy h:m a");

                    let address = `${profile.address.specific_address}, ${profile.address.zipcode}`
                    return [
                        result.id,
                        `${profile.first_name} ${profile.last_name}`,
                        profile.age,
                        profile.gender.toLowerCase() === "m" ? "Male" : "Female",
                        profile.person_type,
                        address === null ? "N/A" : address,
                        profile.mobile === null ? "N/A" : profile.mobile,
                        result.fromLocation === null ? "N/A" : result.fromLocation.toUpperCase(),
                        result.toLocation === null ? "N/A" : result.toLocation.toUpperCase(),
                        dateFns.format(new Date(result.fromDate), "MMM d, yyyy"),
                        dateFns.format(new Date(result.toDate), "MMM d, yyyy"),
                        result,
                    ];
                });
            }

            setIsLoading(false);
            setProfileData(profiles);
        }).catch(e => {
            setIsLoading(false);
        });
    }, []);

    useEffect(() => {
        loadProfiles();
    }, [loadProfiles]);

    return (
        <div className="container mt-4" style={{ minWidth: "1450px" }} >
            <div className="row">
                <ImportForm loadProfiles={loadProfiles} />
                <DataTable loadProfiles={loadProfiles} data={profileData} isLoading={isLoading} columns={columns} options={options} />
                <Footer />
            </div>
            <UpdateProfileForm loadProfiles={loadProfiles} isOpen={updateProfileModal} toggle={toggleUpdateProfileModal} selectedProfile={selectedProfile} />
        </div>
    )
}

export default Content