import React, { useState, useEffect } from 'react'
import Axios from 'axios';
import Swal from 'sweetalert2';
import DateFnsUtils from '@date-io/date-fns';

import {
    Button,
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
    Card,
    CardBody,
    Row,
    Col
} from 'reactstrap';
import TextField from '@material-ui/core/TextField';
import { CircularProgress } from '@material-ui/core';
import InputAdornment from '@material-ui/core/InputAdornment';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faWindowClose, faUser, faSave, faAnchor, faMapMarkedAlt, faShip, faPhoneAlt, faUsers, faTicketAlt, faPeopleArrows } from '@fortawesome/free-solid-svg-icons';

import { useAuth } from "../../../../../context/auth";
import { useUrl } from "../../../../../context/url";

import TypeSelect from '../ProfileFormComponents/TypeSelect';
import GenderSelect from '../ProfileFormComponents/GenderSelect';
import DepartureDate from '../ProfileFormComponents/DepartureDate';
import ArrivalDate from '../ProfileFormComponents/ArrivalDate';
import TransportType from '../ProfileFormComponents/TransportType';
import AccommodationType from '../ProfileFormComponents/AccommodationType';

const UpdateProfileForm = (props) => {

    const authToken = useAuth().authTokens.access_token;
    const urlContext = useUrl();
    const dateFns = new DateFnsUtils();
    const [isProcessing, setIsProcessing] = useState(false);

    const [profileid, setProfileid] = useState(null);

    // Personal
    const [personType, setPersonType] = useState('');
    const [firstName, setFirstName] = useState('');
    const [lastName, setLastName] = useState('');
    const [age, setAge] = useState('');
    const [gender, setGender] = useState('');
    const [mobileNumber, setMobileNumber] = useState('');
    const [email, setEmail] = useState('');
    const [bodyTemperature, setBodyTemperature] = useState('');

    //Address
    const [zipcode, setZipcode] = useState('');
    const [completeAddress, setCompleteAddress] = useState('');

    // Travel Details
    const [origin, setOrigin] = useState('');
    const [destination, setDestination] = useState('');
    const [travelHistory, setTravelHistory] = useState('');

    // Transport Details
    const [shipName, setShipName] = useState('');
    const [voyageRoute, setVoyageRoute] = useState('');
    const [departureDate, setDepartureDateChange] = useState(null);
    const [arrivalDate, setArrivalDateChange] = useState(null);
    const [seatNumber, setSeatNumber] = useState('');
    const [parkingNumber, setParkingNumber] = useState('');
    const [transportNumber, setTransportNumber] = useState('');
    const [transportType, setTransportType] = useState('');
    const [accommodationType, setAccomodationType] = useState('');

    // Companion Details
    const [travelId, setTravelId] = useState('');
    const [childCompanion, setChildCompanion] = useState('');
    const [relativeName, setRelativeName] = useState('');
    const [relativeContact, setRelativeContact] = useState('');

    useEffect(() => {
        if (props.selectedProfile != null) {
            let profile = props.selectedProfile.profile;
            let address = props.selectedProfile.profile.address;
            let traceLogs = props.selectedProfile.profile.trace_logs[0];
            let travel = props.selectedProfile;
            let vehicle_details = JSON.parse(travel.vehicle_details);
            let companion = props.selectedProfile.companions[0];
            let relatives = profile.relatives[0];

            // Personal
            setProfileid(profile.id);
            setFirstName(profile.first_name);
            setLastName(profile.last_name);
            setAge(profile.age);
            setGender(profile.gender.toUpperCase());
            setPersonType(profile.person_type);
            setMobileNumber(profile.mobile);
            setEmail(profile.email);
            setBodyTemperature(props.selectedProfile.temperature);

            // Address
            setZipcode(address.zipcode);
            setCompleteAddress(address.specific_address);

            // Travel
            setOrigin(traceLogs.origin);
            setDestination(traceLogs.destination);
            setDepartureDateChange(dateFns.format(new Date(travel.fromDate), "MMM/d/yyyy h:mm:ss a"));
            setArrivalDateChange(dateFns.format(new Date(travel.toDate), "MMM/d/yyyy h:mm:ss a"));
            setTravelHistory(props.selectedProfile.previous_place_travel);

            // Transport
            setShipName(vehicle_details.transport_vehicle);
            setTransportNumber(vehicle_details.transport_no);
            setTransportType(travel.mode_of_travel_id);
            setSeatNumber(travel.seat_no);
            setParkingNumber(props.selectedProfile.parking_space_no)
            setAccomodationType(props.selectedProfile.accommodation_type);
            setVoyageRoute(props.selectedProfile.route);

            //Companions
            setTravelId(companion.travel_id);
            setChildCompanion(companion.name);
            setRelativeName(relatives.name);
            setRelativeContact(relatives.contact);
        }
    }, [props.selectedProfile, dateFns])


    const updateDataHandler = (e) => {
        e.preventDefault();
        const urlPath = urlContext.getUrl(`carrier/update/${profileid}`);
        setIsProcessing(true);
        let token = authToken;
        let profileData = {

            // Personal
            "person_type": document.querySelector("input[name=personType]").value,
            "first_name": document.querySelector("input[name=first_name]").value,
            "last_name": document.querySelector("input[name=last_name]").value,
            "age": document.querySelector("input[name=age]").value,
            "gender": document.querySelector("input[name=gender]").value,
            "mobile": document.querySelector("input[name=mobile]").value,
            "email": document.querySelector("input[name=email]").value,
            "temperature": document.querySelector("input[name=bodyTemperature]").value,

            // Address
            "zipcode": document.querySelector("input[name=zipcode]").value,
            "specific_address": document.querySelector("input[name=complete_address]").value,

            // Travel
            "origin": document.querySelector("input[name=origin]").value,
            "destination": document.querySelector("input[name=destination]").value,
            "previous_travel": document.querySelector("input[name=travelHistory]").value,
            "departure": dateFns.format(new Date(departureDate), "Y-M-dd H:m:s"),
            "arrival": dateFns.format(new Date(arrivalDate), "Y-M-dd H:m:s"),
            "accommodation_type": document.querySelector("input[name=accommodationType]").value,

            // Transport
            "ship_name": document.querySelector("input[name=shipName]").value,
            "transport_no": document.querySelector("input[name=transportNumber]").value,
            "route": document.querySelector("input[name=voyageRoute]").value,
            "transport_type": document.querySelector("input[name=transportType]").value,
            "seat_no": document.querySelector("input[name=seat_number]").value,
            "parking_space_no": document.querySelector("input[name=parking_space_no]").value,

            // Companion    
            "travel_id": travelId,
            "companion_name":  document.querySelector("input[name=childCompanion]").value,

            // Relative
            "relative_name": document.querySelector("input[name=relativeName]").value,
            "relative_contact": document.querySelector("input[name=relativeContact]").value,
        }

        Axios.put(urlPath, profileData, {
            headers: {
                'Authorization': 'bearer ' + token,
            }
        }).then(result => {
            setIsProcessing(false);
            if (result.data.status) {

                Swal.fire(
                    'Transaction Completed!',
                    'Data Successfully Updated!',
                    'success'
                );

                props.toggle();
                props.loadProfiles();

            } else {

                Swal.fire(
                    'Transaction Failed!',
                    result.data.message,
                    'error'
                );
            }

        }).catch(e => {
            setIsProcessing(false);
            Swal.fire(
                'Transaction Failed!',
                'Failed to Import Data!',
                'error'
            );
        });
    }

    return (
        <div>
            <Modal backdrop={"static"} isOpen={props.isOpen} toggle={props.toggle} className="modal-xl">
                <ModalHeader>
                    Update Profile Record <br />
                    <small className="text-danger p-0 m-0" style={{ right: "0" }}>Note: Fields with * requires a value</small>
                </ModalHeader>
                <form onSubmit={(e) => updateDataHandler(e)}>
                    <ModalBody className="pt-0">
                        {
                            isProcessing ?
                                <div className="text-center text-primary" style={{ width: "100%", height: "100%", zIndex: "1001", position: "fixed", top: "0", left: "0" }}>
                                    <div style={{ backgroundColor: "#fff", opacity: "0.2", width: "100%", height: "100%", zIndex: "1000", position: "fixed", top: "0", left: "0" }}></div>
                                    <CircularProgress style={{ marginTop: "20%", zIndex: "1500", }} />
                                    <h4>Submitting updates...</h4>
                                </div>
                                :
                                null
                        }
                        <Row className="p-2">
                            <Col xs={12}>
                                <Row>
                                    <Col xs={12} className="text-left mt-0">
                                        <Card className="p-0" style={{ border: "none" }}>
                                            <CardBody className="m-0  p-0 pl-2 pr-2">
                                                <div className="pt-2 pb-2">
                                                    <TextField
                                                        className="mr-2"
                                                        onChange={(e) => setShipName(e.target.value)}
                                                        defaultValue={shipName}
                                                        InputProps={{
                                                            startAdornment: (<InputAdornment position="start"><FontAwesomeIcon icon={faShip} /></InputAdornment>)
                                                        }}
                                                        name="shipName"
                                                        label="Ship Name"
                                                        required
                                                    />
                                                    <TextField
                                                        className="mr-2"
                                                        onChange={(e) => setTransportNumber(e.target.value)}
                                                        defaultValue={transportNumber}
                                                        InputProps={{
                                                            startAdornment: (<InputAdornment position="start"><FontAwesomeIcon icon={faAnchor} /></InputAdornment>)
                                                        }}
                                                        name="transportNumber"
                                                        label="Voyage Number"
                                                        required
                                                    />
                                                    <TextField
                                                        className="mr-2"
                                                        defaultValue={seatNumber}
                                                        onChange={(e) => setSeatNumber(e.target.value)}
                                                        InputProps={{
                                                            startAdornment: (<InputAdornment position="start"><FontAwesomeIcon icon={faTicketAlt} /></InputAdornment>)
                                                        }}
                                                        name="seat_number"
                                                        label="Seat/Bunk Number"
                                                        required
                                                    />
                                                </div>
                                            </CardBody>
                                        </Card>
                                    </Col>
                                </Row>
                            </Col>
                            <Col xs={12} className="mt-3">
                                <Row>
                                    <Col xs={4}>
                                        <h5 className="text-primary"><FontAwesomeIcon icon={faUser} /> Personal Details</h5>
                                        <Row>
                                            <Col xs={12}>
                                                <Card className="p-1">
                                                    <CardBody className="pt-1">
                                                        <div>
                                                            <TypeSelect personType={personType} />
                                                        </div>
                                                        <div>
                                                            <TextField
                                                                name="first_name"
                                                                label="First Name"
                                                                defaultValue={firstName}
                                                                onChange={(e) => setFirstName(e.target.value)}
                                                                fullWidth
                                                                required
                                                            />
                                                        </div>
                                                        <div>
                                                            <TextField
                                                                name="last_name"
                                                                label="Last Name"
                                                                defaultValue={lastName}
                                                                onChange={(e) => setLastName(e.target.value)}
                                                                fullWidth
                                                                required
                                                            />
                                                        </div>
                                                        <div>
                                                            <TextField
                                                                type="number"
                                                                name="age"
                                                                label="Age"
                                                                defaultValue={age}
                                                                onChange={(e) => setAge(e.target.value)}
                                                                fullWidth
                                                                required
                                                            />
                                                        </div>
                                                        <div>
                                                            <GenderSelect gender={gender} />
                                                        </div>
                                                        <div className="mb-4">
                                                            <TextField
                                                                type="number"
                                                                name="bodyTemperature"
                                                                label="Body Temperature"
                                                                defaultValue={bodyTemperature}
                                                                onChange={(e) => setBodyTemperature(e.target.value)}
                                                                fullWidth
                                                                required
                                                            />
                                                        </div>
                                                    </CardBody>
                                                </Card>
                                            </Col>
                                        </Row>
                                    </Col>
                                    <Col xs={4} className="p-0">
                                        <h5 className="text-primary"><FontAwesomeIcon icon={faPhoneAlt} /> Contact Details</h5>
                                        <Row>
                                            <Col xs={12}>
                                                <Card className="p-1">
                                                    <CardBody className="pt-1">
                                                        <div>
                                                            <TextField
                                                                name="mobile"
                                                                type="number"
                                                                label="Mobile Number"
                                                                defaultValue={mobileNumber}
                                                                onChange={(e) => setMobileNumber(e.target.value)}
                                                                fullWidth
                                                                required
                                                            />
                                                        </div>
                                                        <div>
                                                            <TextField
                                                                type="email"
                                                                name="email"
                                                                label="Email Address"
                                                                defaultValue={email}
                                                                onChange={(e) => setEmail(e.target.value)}
                                                                fullWidth
                                                            />
                                                        </div>
                                                    </CardBody>
                                                </Card>
                                            </Col>
                                        </Row>
                                        <h5 className="text-primary mt-1"><FontAwesomeIcon icon={faMapMarkedAlt} /> Address</h5>
                                        <Row>
                                            <Col xs={12}>
                                                <Card className="p-1">
                                                    <CardBody className="pt-1">
                                                        <div className="mt-4">
                                                            <TextField
                                                                name="zipcode"
                                                                label="Zipcode"
                                                                defaultValue={zipcode}
                                                                onChange={(e) => setZipcode(e.target.value)}
                                                                fullWidth
                                                                required
                                                            />
                                                        </div>
                                                        <div className="mb-4">
                                                            <TextField
                                                                label="Complete Address"
                                                                name="complete_address"
                                                                defaultValue={completeAddress}
                                                                onChange={(e) => setCompleteAddress(e.target.value)}
                                                                fullWidth
                                                                required
                                                            />
                                                        </div>
                                                    </CardBody>
                                                </Card>
                                            </Col>
                                        </Row>
                                    </Col>
                                    <Col xs={4}>
                                        <h5 className="text-primary"><FontAwesomeIcon icon={faUsers} /> Companion Details</h5>
                                        <Row>
                                            <Col xs={12}>
                                                <Card className="p-1">
                                                    <CardBody className="pt-1">
                                                        <div className="mt-2 mb-2">
                                                            <TextField
                                                                label="Child Companion"
                                                                name="childCompanion"
                                                                defaultValue={childCompanion}
                                                                helperText="Use comma (,) as separator for multiple child"
                                                                onChange={(e) => setChildCompanion(e.target.value)}
                                                                fullWidth
                                                            />
                                                        </div>
                                                    </CardBody>
                                                </Card>
                                            </Col>
                                        </Row>
                                        <h5 className="text-primary mt-1"><FontAwesomeIcon icon={faPeopleArrows} /> Nearest Relative</h5>
                                        <Row>
                                            <Col xs={12}>
                                                <Card className="p-1">
                                                    <CardBody className="pt-1">
                                                        <div className="mt-4">
                                                            <TextField
                                                                label="Name"
                                                                name="relativeName"
                                                                defaultValue={relativeName}
                                                                onChange={(e) => setRelativeName(e.target.value)}
                                                                fullWidth
                                                                required
                                                            />
                                                        </div>
                                                        <div className="mb-4">
                                                            <TextField
                                                                type="number"
                                                                label="Contact Number"
                                                                name="relativeContact"
                                                                defaultValue={relativeContact}
                                                                onChange={(e) => setRelativeContact(e.target.value)}
                                                                fullWidth
                                                                required
                                                            />
                                                        </div>
                                                    </CardBody>
                                                </Card>
                                            </Col>
                                        </Row>
                                    </Col>
                                </Row>
                            </Col>
                            <Col xs={12} className="pt-2">
                                <Row>
                                    <Col xs={8}>
                                        <h5 className="text-primary"><FontAwesomeIcon icon={faShip} /> Travel Details</h5>
                                        <Row>
                                            <Col xs={12} className="pr-0">
                                                <Card className="p-1">
                                                    <CardBody className="pt-1">
                                                        <Row>
                                                            <Col xs={6}>
                                                                <div>
                                                                    <TextField
                                                                        label="Port of Embarkation"
                                                                        name="origin"
                                                                        defaultValue={origin}
                                                                        onChange={(e) => setOrigin(e.target.value)}
                                                                        fullWidth
                                                                        required
                                                                    />
                                                                </div>
                                                                <div>
                                                                    <TextField
                                                                        label="Port of Dis-embarkation"
                                                                        name="destination"
                                                                        defaultValue={destination}
                                                                        onChange={(e) => setDestination(e.target.value)}
                                                                        fullWidth
                                                                        required
                                                                    />
                                                                </div>
                                                                <div>
                                                                    <TextField
                                                                        label="Place of Travel History"
                                                                        name="travelHistory"
                                                                        helperText="( within 14 Days )"
                                                                        defaultValue={travelHistory}
                                                                        onChange={(e) => setTravelHistory(e.target.value)}
                                                                        fullWidth
                                                                        required
                                                                    />
                                                                </div>
                                                            </Col>
                                                            <Col xs={6}>
                                                                <div>
                                                                    <DepartureDate departureDate={departureDate} setDepartureDateChange={setDepartureDateChange} />
                                                                </div>
                                                                <div>
                                                                    <ArrivalDate arrivalDate={arrivalDate} setArrivalDateChange={setArrivalDateChange} />
                                                                </div>
                                                                <div>
                                                                    <AccommodationType accommodationType={accommodationType} />
                                                                </div>
                                                            </Col>
                                                        </Row>
                                                    </CardBody>
                                                </Card>
                                            </Col>
                                        </Row>
                                    </Col>
                                    <Col xs={4}>
                                        <h5 className="text-primary"><FontAwesomeIcon icon={faAnchor} /> Other Transport Details</h5>
                                        <Row>
                                            <Col xs={12}>
                                                <Card className="p-1">
                                                    <CardBody className="pt-1">
                                                        <div>
                                                            <TextField
                                                                name="voyageRoute"
                                                                label="Voyage Route"
                                                                defaultValue={voyageRoute}
                                                                onChange={(e) => setVoyageRoute(e.target.value)}
                                                                fullWidth
                                                                required
                                                            />
                                                        </div>
                                                        <div>
                                                            <TransportType transportType={transportType} setTransportType={setTransportType} />
                                                        </div>
                                                        <div>
                                                            <TextField
                                                                name="parking_space_no"
                                                                label="Parking Space Number"
                                                                defaultValue={parkingNumber}
                                                                helperText="( if with service vehicle )"
                                                                onChange={(e) => setParkingNumber(e.target.value)}
                                                                fullWidth
                                                            />
                                                        </div>
                                                    </CardBody>
                                                </Card>
                                            </Col>
                                        </Row>
                                    </Col>
                                </Row>
                            </Col>
                        </Row>
                    </ModalBody>
                    <ModalFooter>
                        <Button type="submit" color="primary" disabled={isProcessing} style={{ minWidth: "120px" }}>
                            <FontAwesomeIcon icon={faSave} /> Save
                        </Button>{' '}
                        <Button color="danger" onClick={props.toggle} disabled={isProcessing} style={{ minWidth: "120px" }}>
                            <FontAwesomeIcon icon={faWindowClose} /> Close
                    </Button>
                    </ModalFooter>
                </form>
            </Modal>
        </div>
    );
}

export default UpdateProfileForm;