import React, { useState } from 'react';
import Axios from 'axios';
import Swal from 'sweetalert2';
import DateFnsUtils from '@date-io/date-fns';

import {
    Button,
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
    Card,
    CardBody,
    Row,
    Input
} from 'reactstrap';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import CircularProgress from '@material-ui/core/CircularProgress';
import { MuiPickersUtilsProvider, KeyboardDateTimePicker } from '@material-ui/pickers';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFileImport, faWindowClose, faCalendarDay, faAnchor, faFileExcel } from '@fortawesome/free-solid-svg-icons';

import { useAuth } from "../../../../../context/auth";
import { useUrl } from "../../../../../context/url";


const ImportProfilesForm = (props) => {
    const authToken = useAuth().authTokens.access_token;
    const urlPath = useUrl().getUrl('store/import/profiles');
    const [isProcessing, setIsProcessing] = useState(false);
    const [excelFile, setExcelFile] = useState(null);
    const [departureDate, setDepartureDateChange] = useState(null);
    const [arrivalDate, setArrivalDateChange] = useState(null);
    const [transportNumber, setTransportNumber] = useState("");
    const [shipName, setShipName] = useState("");
    const [transportType, setTransportType] = useState("");
    const [voyageRoute, setVoyageRoute] = useState("");

    const dFormat = new DateFnsUtils();

    const transportOptions = [
        {
            value: "2",
            label: "Ship"
        }
    ]

    const importFileHandler = (e) => {

        e.preventDefault();
        setIsProcessing(true);
        let token = authToken;
        let formData = new FormData();

        formData.append("profileFile", excelFile);
        formData.append("departure", dFormat.format(departureDate, "Y-M-dd H:m:s"));
        formData.append("arrival", dFormat.format(arrivalDate, "Y-M-dd H:m:s"));
        formData.append("transport_no", transportNumber);
        formData.append("ship_name", shipName);
        formData.append("transport_type", transportType);
        formData.append("route", voyageRoute);

        Axios.post(urlPath, formData, {
            headers: {
                'Authorization': 'bearer ' + token,
                'Content-Type': 'multipart/form-data'
            }
        }).then(result => {
            if (result.data.status) {
                setIsProcessing(false);
                setExcelFile(null);
                setDepartureDateChange(null);
                setArrivalDateChange(null);
                setTransportNumber("");
                setTransportType("");
                setVoyageRoute("");

                Swal.fire(
                    'Transaction Completed!',
                    'Data Successfully Imported!',
                    'success'
                );

                props.toggle();
                props.loadProfiles();
            } else {
                setIsProcessing(false);
                Swal.fire(
                    'Transaction Failed!',
                    result.data.message,
                    'error'
                )
            }
        }).catch(e => {
            setIsProcessing(false);

            Swal.fire(
                'Transaction Failed!',
                'Failed to Import Data!',
                'error'
            )

        });
    }

    return (
        <div>
            <Modal backdrop={"static"} isOpen={props.isOpen} toggle={props.toggle} className="modal-lg">
                <ModalHeader>Multiple Profiles Import</ModalHeader>
                <form onSubmit={(e) => importFileHandler(e)} encType="multipart/form-data" id="excelFile">
                    <ModalBody>
                        {
                            isProcessing ?
                                <div className="text-center text-primary" style={{ width: "100%", height: "100%", zIndex: "1001", position: "fixed", top: "0", left: "0" }}>
                                    <div style={{ backgroundColor: "#fff", opacity: "0.2", width: "100%", height: "100%", zIndex: "1000", position: "fixed", top: "0", left: "0" }}></div>
                                    <CircularProgress style={{ marginTop: "20%", zIndex: "1500", }} />
                                    <h4>Submitting multiple records...</h4>
                                </div>
                                :
                                null
                        }
                        <Row className="p-2">
                            <div className="col-md-6 p-2">
                                <h5 className="text-primary"><FontAwesomeIcon icon={faCalendarDay} /> Travel Dates</h5>
                                <Card className="p-2">
                                    <CardBody className="p-2 mt-5">
                                        <div className="m-0">
                                            <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                                <KeyboardDateTimePicker
                                                    value={departureDate}
                                                    onChange={setDepartureDateChange}
                                                    label="ETD(Est. Time of Departure)"
                                                    format="MMM/d/yyyy h:mm:ss a"
                                                    required
                                                    disablePast
                                                    clearable
                                                    hideTabs
                                                    showTodayButton
                                                    fullWidth
                                                />
                                            </MuiPickersUtilsProvider>
                                        </div>
                                        <div className="mt-2 mb-5">
                                            <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                                <KeyboardDateTimePicker
                                                    value={arrivalDate}
                                                    onChange={setArrivalDateChange}
                                                    label="ETA(Est. Time of Arrival)"
                                                    format="MMM/d/yyyy h:mm:ss a"
                                                    required
                                                    disablePast
                                                    clearable
                                                    hideTabs
                                                    showTodayButton
                                                    fullWidth
                                                />
                                            </MuiPickersUtilsProvider>
                                        </div>
                                    </CardBody>
                                </Card>
                            </div>
                            <div className="col-md-6 p-2">
                                <h5 className="text-primary"><FontAwesomeIcon icon={faAnchor} /> Transport Details</h5>
                                <Card className="p-2">
                                    <CardBody className="p-1 m-1">
                                        <div>
                                            <TextField
                                                value={transportNumber}
                                                onChange={(e) => setTransportNumber(e.target.value)}
                                                label="Voyage Number"
                                                fullWidth
                                                required
                                            />
                                        </div>
                                        <div>
                                            <TextField
                                                value={shipName}
                                                onChange={(e) => setShipName(e.target.value)}
                                                label="Ship Name"
                                                fullWidth
                                                required
                                            />
                                        </div>
                                        <div>
                                            <TextField
                                                label="Voyage Route"
                                                value={voyageRoute}
                                                onChange={(e) => setVoyageRoute(e.target.value)}
                                                fullWidth
                                                required
                                            />
                                        </div>
                                        <div className="mb-2">
                                            <TextField
                                                select
                                                label="Select Transport Type"
                                                value={transportType}
                                                onChange={(e) => setTransportType(e.target.value)}
                                                fullWidth
                                                required
                                            >
                                                {transportOptions.map((option) => (
                                                    <MenuItem key={option.value} value={option.value}>
                                                        {option.label}
                                                    </MenuItem>
                                                ))}
                                            </TextField>
                                        </div>
                                    </CardBody>
                                </Card>
                            </div>
                            <div className="col-md-12 pt-2">
                                <h5 className="text-success">
                                    <FontAwesomeIcon icon={faFileExcel} /> Excel File
                                    <span className="text-danger ml-1" style={{ fontSize: '15px' }}>Note: Use the provided template</span>
                                </h5>
                                <Card>
                                    <CardBody className="p-1 m-0">
                                        <div className="m-0">
                                            <Input
                                                accept=".xlsx,.xls"
                                                id="raised-button-file"
                                                type="file"
                                                onChange={(e) => setExcelFile(e.target.files[0])}
                                                disabled={isProcessing}
                                                required
                                            />
                                        </div>
                                    </CardBody>
                                </Card>
                            </div>
                        </Row>

                    </ModalBody>
                    <ModalFooter>
                        <Button type="submit" color="primary" disabled={isProcessing} style={{ minWidth: "120px" }}>
                            <FontAwesomeIcon icon={faFileImport} /> Import
                        </Button>{' '}
                        <Button color="danger" onClick={props.toggle} disabled={isProcessing} style={{ minWidth: "120px" }}>
                            <FontAwesomeIcon icon={faWindowClose} /> Cancel
                    </Button>
                    </ModalFooter>
                </form>
            </Modal>
        </div>
    );
}

export default ImportProfilesForm;