import React, { useState } from 'react';
import { Card, CardBody } from 'reactstrap';

import ImportProfilesForm from './Modals/ImportProfilesForm';
import ProfileForm from './Modals/ProfileForm';

import FileTemplate from '../../../../assets/file/manifest-template.xlsx';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFileDownload, faUserPlus, faUsers } from '@fortawesome/free-solid-svg-icons';
import Button from '@material-ui/core/Button';

const ImportForm = (props) => {
    const [importModal, setVisibleImportModal] = useState(false);
    const toggleImportModal = () => setVisibleImportModal(!importModal);
    const [newProfileModal, setVisibleNewProfileModal] = useState(false);
    const toggleNewProfileModal = () => setVisibleNewProfileModal(!newProfileModal);

    return (
        <div className="col-md-4 p-0">
            <div className="col-md-12">
                <Card className="mb-1" color="light" style={{ borderRadius: '0px' }}>
                    <CardBody className="p-1">
                        <div className="row pr-3 pl-3">
                            <div className="col-md-4 p-1 m-0">
                                <a href={FileTemplate} download="template.xlsx">
                                    <Button variant="contained" color="primary" fullWidth>
                                        <span style={{ fontSize: '0.9rem' }}><FontAwesomeIcon icon={faFileDownload} /> Download</span>
                                    </Button>
                                </a>
                            </div>
                            <div className="col-md-4 p-1 m-0">
                                <Button onClick={toggleImportModal} variant="contained" color="primary" fullWidth>
                                    <span style={{ fontSize: '0.9rem' }}><FontAwesomeIcon icon={faUsers} /> Import</span>
                                </Button>
                            </div>
                            <div className="col-md-4 p-1 m-0">
                                <Button onClick={toggleNewProfileModal} variant="contained" color="primary" fullWidth>
                                    <span style={{ fontSize: '0.9rem' }}><FontAwesomeIcon icon={faUserPlus} /> Passenger</span>
                                </Button>
                            </div>
                        </div>
                    </CardBody>
                </Card>
            </div>
            <div>
                <ImportProfilesForm loadProfiles={props.loadProfiles} isOpen={importModal} toggle={toggleImportModal} />
                <ProfileForm loadProfiles={props.loadProfiles} isOpen={newProfileModal} toggle={toggleNewProfileModal} />
            </div>
        </div>
    )

}

export default ImportForm
